<template>
    <div class="block">
        <!--工具条-->
        <!--栅格布局 -->
        <div class="toolbar">
            <el-col :span="4">
                <!--<span class="">地区:</span>-->
                <el-cascader placeholder='省/市/区' change-on-select :options="options" v-model="selectedOptions" @change="dizhiChange">
                </el-cascader>
                <!--<p>省I1D:{{obj[0]}}市ID:{{obj[1]}}区ID:{{obj[2]}}</p>-->
            </el-col>
            <el-col :span="4">
                <!--<span class="">类型:</span>-->
                <el-cascader placeholder='小学' change-on-select :options="options" v-model="selectedOptions" @change="dizhiChange">
                </el-cascader>
            </el-col>
            <el-col :span="4">
                <!--<span class="">状态:</span>-->
                <el-cascader placeholder='启用' change-on-select :options="options" v-model="selectedOptions" @change="dizhiChange">
                </el-cascader>
            </el-col>
            <el-col :span="4">
                <!--<span class="">关键字:</span>-->
                <el-cascader placeholder='输入编号和名称' change-on-select :options="options" v-model="selectedOptions" @change="dizhiChange">
                </el-cascader>
            </el-col>
            <el-col :span="8">
                <el-button type="primary" size="large">查询</el-button>
                <el-button type="primary">导入</el-button>
                <el-button type="primary" size="small">导出</el-button>
                <el-button type="primary" size="mini">超小按钮</el-button>
            </el-col>
        </div>
    
        <!--显示列表-->
        <el-table highlight-current-row v-loading="loading_1" element-loading-text="拼命加载中" :data="showList" style="width: 100%;text-align:left">
            <el-table-column label="学校名称" prop="name" min-width="200">
            </el-table-column>
            <el-table-column prop="id" label="学校编号" min-width="150">
            </el-table-column>
            <el-table-column label="授权" prop="accredit" :formatter="tableStatus" min-width="150" sortable>
            </el-table-column>
            <el-table-column label="配置" min-width="220">
                <template scope="scope">
                    <el-button size="small">屏蔽授权</el-button>
                    <el-button type="primary" size="small" @click="handleEdit(scope.$index, scope.row)">查看配置</el-button>
                </template>
            </el-table-column>
        </el-table>
        <!--编辑页面-->
        <el-dialog title="代理商信息修改" v-model="dialogVisible" :close-on-click-modal="false">
            <el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
                <el-form-item label="学校名称" prop="name">
                    <el-input v-model="editForm.name" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="代理账号" prop="id">
                    <el-input v-model="editForm.id" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="公司电话" prop="phone">
                    <el-input v-model="editForm.phone" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="公司地址">
                    <el-input type="textarea" v-model="editForm.address"></el-input>
                </el-form-item>
                <el-form-item label="创建时间" prop="createTime">
                    <!--<el-input v-model="editForm.time" auto-complete="off"></el-input>-->
                    <el-date-picker format v-model="editForm.createTime" type="date" placeholder="选择时间">
                    </el-date-picker>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button :plain="true" type="info" @click.native="editSubmit">确 定</el-button>
            </span>
        </el-dialog>
    
        <div class="block">
            <span class="demonstration">完整功能</span>
            <el-pagination @size-change="pagingSizeChange" @current-change="pagingCurrentChange" :page-sizes="[5, 10, 15, 20]" :page-size="10" layout="total, sizes, prev, pager, next, jumper" :total="clientList.length">
            </el-pagination>
        </div>
    </div>
</template>
<script>
import options from '../../../static/district'  //引入三级地址数据
import axios from 'axios'
import util from '../../utils/utils'  //时间格式化方法
export default {
    data() {
        return {
            //表格相关数据
            clientList: [{
                id: '',
                name: '',
                accredit: '',
            },],
            //三级地址数据
            options,
            obj: {
                0: 'null',
                1: 'null',
                2: 'null'
            },
            selectedOptions: [],
            //筛选
            filters: {
                name: ''
            },
            //加载框
            loading_1: false,
            //分页相关
            showList: [],
            page_size: 10,
            currentPage: "",
            star: 0,
            end: 10,
            //编辑页面
            editFormRules: {
                name: [
                    { required: false, message: '请输入姓名', trigger: 'blur' }
                ]
            },
            dialogVisible: false,//编辑页面是否显示
            //编辑页数据
            editForm: {
                id: '',
                create_at: ''
            },
        };
    },
    methods: {
        dizhiChange: function (value) {
            // console.log(value);
            // console.log(this.obj);
            // for (var i = 0; i < 3; i++) {
            //     this.obj[i] = label[i]
            // }

        },
        //显示编辑界面
        handleEdit: function (index, row) {
            this.dialogVisible = true;
            this.editForm = Object.assign({}, row);
            // console.log(this.editForm)
        },
        tableStatus: function (row, column) {
            return row.status == 1 ? '启用' : row.status == 0 ? '屏蔽' : '未定义';
        },
        tableSex: function (row, column) {
            return row.status == 1 ? '男' : row.status == 0 ? '女' : '未定义';
        },
        editSubmit: function () {
            this.$refs.editForm.validate((valid) => {
                if (valid) {
                    this.$confirm('确认提交吗？', '提示', {}).then(() => {
                        let para = Object.assign({}, this.editForm);
                        para.time = (!para.time || para.time == '') ? '' : util.formatDate.format(new Date(para.time), 'yyyy-MM-dd');
                        // console.log(para.time)
                        // console.log(para)
                        this.dialogVisible = false;
                    });
                }
            });
        }
        ,
        //删除
        handleDel: function (index, row) {
            this.$confirm('确认删除该记录吗?', '提示', {
                type: 'warning'
            })
        },
        handleClose(done) {
            this.$confirm('确认关闭？')
                .then(_ => {
                    done();
                })
                .catch(_ => { });
        }
        ,
        //分页方法
        pagingSizeChange(val) {
            console.log(`每页 ${val} 条`);
            this.page_size = val;
            // console.log(this.page_size)
            this.star = (this.currentPage - 1) * this.page_size;
            this.end = this.star + this.page_size;
            this.showList = this.clientList.slice(this.star, this.end)
        },
        pagingCurrentChange(val) {
            console.log(`当前页: ${val}`);
            this.currentPage = val;
            this.star = (val - 1) * this.page_size;
            this.end = this.star + this.page_size;
            this.showList = this.clientList.slice(this.star, this.end)
        },
    },
    mounted() {


        //模拟数据
        this.clientList = [
            {
                id: 'S_1001',
                name: '深圳中学',
                accredit: '1',
            },
            {
                id: 'S_1001',
                name: '深圳中学',
                accredit: '1',
            },
            {
                id: 'S_1001',
                name: '深圳中学',
                accredit: '0',
            }, {
                id: 'S_1001',
                name: '深圳中学',
                accredit: '1',
            }, {
                id: 'S_1001',
                name: '深圳中学',
                accredit: '0',
            }, {
                id: 'S_1001',
                name: '深圳中学',
                accredit: '1',
            },
            {
                id: 'S_1001',
                name: '深圳中学',
                accredit: '1',
            },
            {
                id: 'S_1001',
                name: '深圳中学',
                accredit: '0',
            }, {
                id: 'S_1001',
                name: '深圳中学',
                accredit: '1',
            }, {
                id: 'S_1001',
                name: '深圳中学',
                accredit: '0',
            }, {
                id: 'S_1001',
                name: '深圳中学',
                accredit: '1',
            },
            {
                id: 'S_1001',
                name: '深圳中学',
                accredit: '1',
            },
            {
                id: 'S_1001',
                name: '深圳中学',
                accredit: '0',
            }, {
                id: 'S_1001',
                name: '深圳中学',
                accredit: '1',
            }, {
                id: 'S_1001',
                name: '深圳中学',
                accredit: '0',
            }, {
                id: 'S_1001',
                name: '深圳中学',
                accredit: '1',
            },
            {
                id: 'S_1001',
                name: '深圳中学',
                accredit: '1',
            },
            {
                id: 'S_1001',
                name: '深圳中学',
                accredit: '0',
            }, {
                id: 'S_1001',
                name: '深圳中学',
                accredit: '1',
            }, {
                id: 'S_1001',
                name: '深圳中学',
                accredit: '0',
            }, {
                id: 'S_1001',
                name: '深圳中学',
                accredit: '1',
            },
            {
                id: 'S_1001',
                name: '深圳中学',
                accredit: '1',
            },
            {
                id: 'S_1001',
                name: '深圳中学',
                accredit: '0',
            }, {
                id: 'S_1001',
                name: '深圳中学',
                accredit: '1',
            }, {
                id: 'S_1001',
                name: '深圳中学',
                accredit: '0',
            },
        ]





        //调用方法
        this.showList = this.clientList.slice(this.star, this.end)
    }
};
</script>
 <style lang="scss">
.block {
    .toolbar {
        margin: 10px 0;
        padding: 20px 0px;
        height: 40px;
        width: 100%;
        background-color: #f2f2f2;
    }
}


.el-table__header-wrapper {
    table {
        th {
            text-align: center;
        }
        ;
    }
}

.el-table__body-wrapper {
    table {
        tr {
            td {
                text-align: center;
            }
        }
    }
}
</style>



